.form__item {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  padding: 0;
}

.form__item:first-of-type {
  margin-top: 0;
}
 
.field {
  margin-top: 10px;
}

.form__item .field:first-of-type {
  margin-top: 0;
}

.form__item .field:last-of-type {
  margin: 10px 0;
}

.btn-submit {
  background-color: #ffff;
  margin: 20px 0;
  border-radius: 5px;
}
